@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
  font-family: 'Shabnam', sans-serif;
}

.c {
  height: 100%;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  position: absolute;
}

.c-wrapper {
  padding: 50px;
  margin-top: 22vh;
  margin-bottom: 30vh;
  display: flex;
}

.c-left {
  flex: 1;
}

.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.c-title {
  font-size: 50px;
  width: 80%;
  background: linear-gradient(#257df1, #9b18e7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
 
}

.c-desc {
  font-weight: 200;
  color: #FFFF;
  text-align: right;
}

.c-form {
  margin-top: 20px;
}

.c-userForm {
  width: 80%;
  height: 50px;
  text-align: right;
  border: none;
  border: 1px solid rgba(166, 37, 241, 0.9);
  margin: 10px 0px;
  font-family: 'ShabnamLight', sans-serif;
  border-radius: 20px;
  font-size: 14px;
  padding-right: 20px;
  color: #FFFF;
  background-color: transparent;
}
.c-userForm::-webkit-input-placeholder {
  color: #b0b0b0;
}
.c-userMessage::-webkit-input-placeholder {
  color: #b0b0b0;
}

.c-userMessage {
  width: 80%;
  margin: 10px 0px;
  font-size: 15px;
  border: 1px solid rgba(166, 37, 241, 0.9);
  padding-right: 20px;
  padding-top: 10px;
  text-align: right;
  border-radius: 20px;
  font-family: 'ShabnamLight', sans-serif;
  font-weight: 400;
  color: white;
  background-color: transparent;
}

input[type=text] {
  color: white;
}


.c-submitBtn {
  border: none;
  width: 84%;
  padding: 15px;
  background: linear-gradient(to left ,#3a9cff, #974ec8);
  color: white;
  border-radius: 20px;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Shabnam', sans-serif;
  cursor: pointer;
}

@media screen and (max-width: 510px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }

  .c-title {
    font-size: 30px;
    width: 100%;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }

  .c-desc {
    display: none;
  }

  .c-form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  .c-userForm {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
    width: 100%;
  }
  .c-userMessage {
    width: 100%;
  }
  .c-submitBtn {
    margin-top: 10px;
    width: 100%;
  }
}
