
@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&display=swap');

  
  :root {
    --light-clr-cont: #fff;
    --dark-clr-cont: #FFF;
    --brand-clr-cont: #5865f2;

    --general-font-size-cont: 2rem;
    --primary-title-font-size-cont: 4.8rem;
    --secondary-title-font-size-cont: 4rem;
    --main-font-family-light-cont: 'ShabnamLight', sans-serif;
    --main-font-family-medium-cont: 'Shabnam', sans-serif;
    --main-font-family-bold-cont: 'ShabnamBold', sans-serif;
    --num-of-grid-columns-cont: 4;
    --section-spacing-cont: 100px;
    --container-cont-width-cont: 126rem;
  }

  .grid-image {
    max-width: 100%;
    display: block;
  
  }


  .down-header-title {
     margin-bottom: 6%;
  }
  .container-cont {
    max-width: var(--container-cont-width-cont);
    margin: 0 auto;

  }
  
  
  .btn {
    border-radius: 2rem;
    transition: all .2s ease;
  }
  .btn:hover {
    box-shadow: 0 0.8rem 1.5rem rgb(0, 0, 0, 0.2);
    transition: all .2s ease;
  }
  
  
  .row-cont {
    display: grid;
    grid-template-columns: repeat(var(--num-of-grid-columns-cont), 1fr);
    gap: 0 2rem;
    padding: var(--section-spacing-cont);
  }
  
  
  .title {
    font-family: 'ShabnamBold', sans-serif;
    color: #FFFF;
  }

  .btn-large {
    padding: 1.6rem 3.2rem;
    border-radius: 2.8rem;
  }
  
  
  .grid-image {
    margin-top: 2.4rem;
    grid-column: span 4;
  }
  
  .text-group {
    margin-top: 2rem;
    grid-column: span 4;
    text-align: right;
  }
  
  .secondary-title {
    font-size: 5vh;
    font-weight: 900;
    color: #FFF;
    line-height: 120%;
    font-family: 'ShabnamBold', sans-serif;
  }
  
  .text-group p {
    margin-top: 2rem;
    line-height: 1.625;
    font-size: 20px;
    font-family: 'Shabnam', sans-serif;
    font-weight: 300;
  }
  
  /*  voice channel */
  .voice-channel {
    
  }
  
  .wave {
   
    display: block;
  }
  
  .wave-path {
    animation: wave-animation 20s linear infinite alternate;
  }
  
