    0% {
      d: path(
        "M826.337463,25.5396311 C670.970254,58.655965 603.696181,68.7870267 447.802481,35.1443383 C293.342778,1.81111414 137.33377,1.81111414 0,1.81111414 L0,150 L1920,150 L1920,1.81111414 C1739.53523,-16.6853983 1679.86404,73.1607868 1389.7826,37.4859505 C1099.70117,1.81111414 981.704672,-7.57670281 826.337463,25.5396311 Z"
      );
    }
    50% {
      d: path(
        "M655.558582,28.4898877 C500.191373,61.6062216 422.833785,83.5422763 266.940085,49.8995879 C112.480383,16.5663637 84.0992497,8.02840272 0,44.7243294 L0,150.727527 L1920,150.727527 L1920,49.8995879 C1580.91676,-34.8808285 1438.10804,73.6768193 1148.0266,38.0019829 C857.945166,2.32714659 810.925791,-4.62644617 655.558582,28.4898877 Z"
      );
    }
    100% {
      d: path(
        "M842.322034,38.0019829 C686.954825,71.1183168 623.386282,53.08497 467.492582,19.4422816 C313.032879,-13.8909426 84.0992497,8.02840272 0,44.7243294 L0,150.727527 L1920,150.727527 L1920,76.8685643 C1512.23604,-35.3595639 1568.70222,67.4289432 1278.62078,31.7541069 C988.539347,-3.92072949 997.689243,4.88564905 842.322034,38.0019829 Z"
      );
    }
  }
  
  .wave-inverted {
    transform: matrix(1, 0, 0, -1, 0, 0);
  }
  
  .video-call {
    
  }
  
  .video-call .row-cont{
    padding-bottom: 8rem;
  }
  
  .video-call .text-group {
    grid-column: span var(--num-of-grid-columns-cont);
    margin-top: 0;
  }
  
  .video-call .text-group p {
    margin-top: 2.4rem;
  }
  
  .video-call .grid-image {
    width: 100%;
    grid-column: span var(--num-of-grid-columns-cont);
    margin-top: 2.4rem;
  }
  
  /* Download */
  
  .download {
  
  }
  
  .download .remove-grid {
    display: block;
    padding-top: 0;
    text-align: center;
  }
  
  .sparkles {
    display: inline-block;
  }
  
  .download h2 {
    font-size: 3.2rem;
    font-family: var(--main-font-family-medium-cont);
  }
  
  .btn-brand {
    background-color: var(--brand-clr-cont);
    color: var(--light-clr-cont);
    transition: background-color 0.5s;
  }
  .download .btn-brand {
    margin-top: 4rem;
    transition: all .2s ease;
  }
  
  .btn-brand:hover {
    background-color: #7983f5;
    transition: all .2s ease;
  }
  
  
  @media screen and (min-width: 768px) {
    :root {
      --primary-title-font-size: 2.5em;
      --secondary-title-font-size: 4.8rem;
      --num-of-grid-columns: 8;
      --section-spacing: 8rem 4rem;
    }
  
    
    .grid-image {
      margin: auto 0;
    }
    .text-group {
      margin-top: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  
    .order-1 {
      order: 1;
    }
  
    .order-2 {
      order: 2;
    }
  
    .video-call {
      text-align: center;
    }
    
  

  }
  
  
  
  @media screen and (min-width:1024px) {
    :root {
      --num-of-grid-columns-cont: 12;
      --section-spacing-cont: 100px;
    }
  
  
    .grid-image{
      grid-column: span 7;
      margin: 0;
    }
    .text-group{
      grid-column: 9/-1;
    }
  
    .order-1{
      grid-column: 1/span 4;
    }
    .order-2{
      grid-column: 6/-1;
    }
  
    .video-call .text-group{
      grid-column: 2/span 10;
    }
  }

  @media screen and (max-width: 425px) {
    .row-cont {
      padding: 50px;
    }
    .secondary-title {
      font-size: 32px;
      font-weight: 900;
      line-height: 120%;
      font-family: 'ShabnamBold', sans-serif;
    }
    .btn-brand {
      display: none;
    }
  }
  @media screen and (min-width: 1800px) {
    
    .secondary-title {
      font-size: 62px;
      
    }
    .text-group p {
      font-size:28px;
    }
  }