:root {
  --primary: #1888ff;
}


.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  background-color: var(--primary);
  font-family: 'Shabnam', sans-serif;
}

.btn:hover {
  transition: all 0.3s ease-out;
  background-color: #3de6c7;
  color: rgb(112, 11, 112);
}
