@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}
.hero-section {
  background: linear-gradient(#2b1055, #7597de);
}
body {
  background: #15072c;
  min-height: 100vh;
  overflow-x: hidden;
}


@font-face {
  font-family: "Shabnam";
  src: url("./components/font/Shabnam.ttf") format('woff');
}

@font-face {
  font-family: "ShabnamLight";
  src: url("./components/font/Shabnam-Light.ttf") format('woff');
}

@font-face {
  font-family: "ShabnamBold";
  src: url("./components/font/Shabnam-Bold.ttf") format('woff');
}



@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300;400;500;600;700&display=swap');
.timeDate {
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 300;
    font-size: xx-large;
    position: absolute;
    top: 70%;
    left: 4%;
    color: #FFF; 
    z-index: 80;
}

.PollAndWeather {
  display: grid;
  grid-gap: 6px;
  grid-template-columns: 45% 55%;
  grid-template-rows: auto 1fr auto;
  padding: 2rem;
  grid-gap: 1rem;
}


@media screen and (max-width: 768px) {
  .PollAndWeather {
    display: contents;
    padding: 2rem;
  }
}