@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');



.music-card {
    height: 175px;
    width: 175px;
    border-radius: 14%;
    overflow: hidden;
    
}

.song-title {
    padding: 0rem 1rem !important;
    margin-top: 1vh;
    color: #FFF;
    font-weight: 500;
    font-family: 'Quicksand', sans-serif;
}

.song-artist {
    color: rgba(255, 255, 255, 60%);
    font-weight: 300;
    font-size: small;
    margin-left: 0vh;
}


.music-overflow {
    height: 125px;
}



.music-img-top:hover {
    transform: scale(1.1);
    transition: all 0.4s ease;
}

.music-img-top{
    transform: scale(1);
    transition: all 0.4s ease;
}

.play-music-btn {
    transform: scale(1);
    transition: all 0.2s ease;
}
.play-music-btn:hover {
    transform: scale(1.1);
    transition: all 0.2s ease;
}

.bluredDetails {
    height: 100vh;
    width: 100vh;
}


