.music-card-title {
    font-size: 6vh;
    text-decoration: underline red;
    text-underline-offset: 5px;
}

.MainCardandBibilak {
    display: grid;
    grid-template-rows: 50% 50%;
}

.banner_cards_grid {
    display: grid;
    grid-column-gap: 16px;
    column-gap: 16px;
    grid-template-columns: repeat(auto-fill, minmax(248px, 1fr));
    /* overflow-y: hidden; */
    grid-auto-rows: 0;
    grid-template-rows: 1fr;
  }

  
  .discover_cards {
    display: flex;
    overflow: hidden;
    position: relative;
  }
  
  .img-banner-dsv {
      width: 90%;
      height: auto;
     
  }
  
  .discover-card-info {
      position: absolute;
      top: 45%;
      left: 3%;
  }
  .discover-info-title {
      color: #FFF;
      width: 70%;
      height: auto;
      font-size: medium;
      font-weight: 700;
  }

 


.mainDivMiscs {
    display: grid;
    grid-gap: 6px;
    grid-template-columns: 70% 30%;
    grid-template-rows: auto 1fr auto;
    padding: 2rem;
    grid-gap: 1rem;
}
.ahay {
    display: grid;
    grid-column-gap: 16px;
    column-gap: 16px;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    /* overflow-y: hidden; */
    grid-auto-rows: 0;
    grid-template-rows: 1fr;
}
.music-card-main {
    overflow: hidden;
    object-fit: cover;
    padding-right: 1rem;
}

.ahay-title {
    color : #FFF;
    display: none;
    font-size: large;
}

.music-card-img {
    object-fit: cover;
    border-radius: 14%;
    width: 100%;
    height: auto;
}

.mainPage-Msc {
    width: 100%;
}


.card {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 210px 250px 20px;
    grid-template-areas: "image" "text" "stats";
    border-radius: 18px;
    background-color: #1d1d1d;
    color: white;
    box-shadow: 5px 5px 15px rgba(0,0,0,0.9);
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    transition: 0.5s ease;
    cursor: pointer;
    margin: 30px;
    transform-style:preserve-3d;
}

.card-image {
   
    width: 100%;
    height: 30vh;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    
}
.card-img-my {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    width: 100%;
    object-fit: cover;
    height: 35vh;
}

.card-text {
    grid-area: text;
    margin: 25px;
    transform: translateZ(30px);
    text-align: right;
}

.card-text .date {
    color: rgb(255, 7, 110);
    font-size: 13px;
}

.card-text p {
    color: gray;
    font-size: 14px;
    font-weight: 300;
}

.card-text h2 {
    margin-top: 0;
    font-size: 28px;
    margin-bottom: 1rem;
}
.card:hover {
    transform: scale(1.02);
    box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
  }

  .rgb::after {
    content:"";
    background: linear-gradient(45deg,
    #ff0000 0%, 
    #ff9a00 10%,
    #d0de21 20%,
    #4fdc4a 30%, 
    #3fdad8 40%,
    #2fc9e2 50%, 
    #1c7fee 60%, 
    #5f15f2 70%, 
    #ba0cf8 80%, 
    #fb07d9 90%, 
    #ff0000 100%  )
    repeat 0% 0% / 300% 100%;
    position: absolute;
    inset: -3px;

    border-radius: 16px;
    filter: blur(8px);
    transform: translateZ(-1px);
    animation:  rgb 6s linear infinite;
  }

  @keyframes rgb {
      0% {background-position: 0% 50%;}
      50% {background-position: 100% 50%;}
      100% {background-position: 0% 50%;}
  }



  @media screen and (max-width: 1050px)  {
    .mainDivMiscs {
        display: contents;
    }
    .MainCardandBibilak  {
        display: block;
    }
    .ahay-title {
        display: flex;
        margin-top: 1rem;
        margin-bottom: 1rem;
    }
    .card {
        display: none
    }
}


.iWannaDie {
    margin-top: 10vh;
}

.music-hits-img {
    display: inherit;
    border-radius: 10px;
    width: 100%;
    height: auto;
    object-fit: cover;
}

.music-hits {
  padding-right: 2rem;
}

.music-hits-imgTwo {
    display: inherit;
    border-radius: 10px;
    width: 85%;
    height: 100%;
    object-fit: cover;
}

@media screen and (max-width: 768px) {
    .music-hits-imgTwo {
        display: none;
    }
    .div-card {
        display: contents;
    }
}



.discover-cutie {
    position: absolute;
    top: 12%;
    left: 20%;
    height: auto;
    width: 185px;
}



.div-card {
    display: none
}