

.section-nitro
{
	position: relative;
	width: 100%;
	min-height: 100vh;
	padding: 70px;
	display: flex;
	justify-content: center;
	align-items: center;
    
	background: #051536;
    
}
.button-nitro {
    margin-top: 1rem;
}
.content-nitro
{
	position: relative;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.content-nitro .textBox-nitro
{
	position: relative;
	max-width: 600px;
	margin-right: 10px;
	margin-top: 110px;
    
}
.content-nitro .textBox-nitro img
{
	max-width: 290px;
	margin-bottom: 20px;
}
.content-nitro .textBox-nitro p
{
	color: #FFF;
	font-size: 1.2em;
    margin-bottom: 2rem;
}
.content-nitro .textBox-nitro a
{
	display: inline-block;
	margin: 25px 25px 0px 0px;
	padding: 15px 50px;
	color: #f9fafc;
	border-radius: 25px;
	font-size: 1em;
	font-weight: bold;
	text-decoration: none;
	width: 200px;
	text-align: center;
}
.content-nitro .textBox-nitro .trial-nitro a
{
	border: 1px solid #f9fafc;
}
.content-nitro .textBox-nitro .trial-nitro a:hover
{
	background: #888fe8;
	border: 0px;
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), 0 8px 20px rgba(0, 0, 0, 0.2);
}
.content-nitro .textBox-nitro .buy-nitro a
{
	background: #888fe8;
}
.content-nitro .textBox-nitro .buy-nitro a:hover
{
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), 0 8px 20px rgba(0, 0, 0, 0.2);
}
.content-nitro .imgBox-nitro
{
	position: relative;
	width: 500px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.content-nitro .imgBox-nitro img
{
	position: relative;
	max-width: 500px;
	margin-left: 120px;
}
.content-nitro .imgBox-nitro img:hover
{
  animation-name: zoom;
  animation-duration: 20s;
}


@media (max-width: 1000px)
{
	.section-nitro
	{
		padding: 180px 20px;
	}
	
	.content-nitro
	{
		flex-direction: column-reverse;
		margin-left: 10px;
		text-align: center;
	}
	.content-nitro .textBox-nitro,
	.content-nitro .imgBox-nitro
	{
		max-width: 100%;
	}
	.content-nitro .textBox-nitro img
	{
		max-width: 300px;
	}
	.content-nitro .textBox-nitro p
	{
		font-size: 1em;
	}
	.content-nitro .imgBox-nitro img
	{
		width: 180px;
		margin-top: 180px;
		margin-left: 170px;
		padding: 0px;
		position: absolute;
	}
}



.textBOX-NITRO-img {
	width: 500px;
	height: auto;
}

