/*==================== GOOGLE FONTS ====================*/

/*==================== VARIABLES CSS ====================*/
:root {
    --header-height: 3rem;

    /*========== Colors ==========*/
    /* Change favorite color */
    --hue-color: 250; /*Purple 250 - Green 142 - Blue 230 - Pink 340*/

    /* HSL color mode */
    --first-color: hsl(var(--hue-color), 69%, 61%);
    --first-color-second: hsl(var(--hue-color), 69%, 61%);
    --first-color-alt: hsl(var(--hue-color), 57%, 53%);
    --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
    --title-color: hsl(var(--hue-color), 8%, 15%);
    --text-color: hsl(var(--hue-color), 8%, 45%);
    --text-color-light: hsl(var(--hue-color), 8%, 65%);
    --input-color: hsl(var(--hue-color), 70%, 96%);

    --container-color: #fff;
    --scrooll-bar-color: hsl(var(--hue-color), 12%, 90%);
    --scrooll-thumb-color: hsl(var(--hue-color), 12%, 80%);

    /*========== Font and typography ==========*/
    --body-font: "Poppins", sans-serif;

    /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
    --big-font-size: 2rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.893rem;
    --smaller-font-size: 0.75rem;

    /*========== Font weight ==========*/
    --font-medium: 500;
    --font-semi-bold: 600;

    /*========== Margenes Bottom ==========*/
    /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
    --mb-0-25: 0.25rem;
    --mb-0-5: 0.5rem;
    --mb-0-75: 0.75rem;
    --mb-1: 1rem;
    --mb-1-5: 1.5rem;
    --mb-2: 2rem;
    --mb-2-5: 2.5rem;
    --mb-3: 3rem;

    /*========== z index ==========*/
    --z-tooltip: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}

/* Font size for large devices */
@media screen and (min-width: 968px) {
    :root {
        --big-font-size: 3rem;
        --h1-font-size: 2.25rem;
        --h2-font-size: 1.5rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: 0.875rem;
        --smaller-font-size: 0.813rem;
    }
}



/*==================== BASE ====================*/

/*==================== REUSABLE CSS CLASSES ====================*/
.section {
    padding: 2rem 0 4rem;
}

.section__title {
    font-size: 1.5rem;
    font-size: var(--h1-font-size);
    font-family: 'ShabnamBold', sans-serif;
}

.section__subtitle {
    display: block;
    font-size: 0.893rem;
    font-size: var(--small-font-size);
    margin-bottom: 3rem;
    margin-bottom: var(--mb-3);
    font-family: 'ShabnamBold', sans-serif;
}

.section__title,
.section__subtitle {
    text-align: center;
}

/*==================== LAYOUT ====================*/
.container-aut {
    max-width: 768px;
    margin-left: 1.5rem;
    margin-left: var(--mb-1-5);
    margin-right: 1.5rem;
    margin-right: var(--mb-1-5);
}

.grid-aut {
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.header {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    z-index: var(--z-fixed);
    
}

/*==================== NAV ====================*/
.nav {
    max-width: 968px;
    height: 3rem;
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo,
.nav__toggle {
    color: hsl(250, 8%, 15%);
    color: var(--title-color);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.nav__logo:hover {
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

.nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
}

.nav__toggle:hover {
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

@media screen and (max-width: 767px) {
    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
       
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
    }
}

.nav__list {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.893rem;
    font-size: var(--small-font-size);
    color: hsl(250, 8%, 15%);
    color: var(--title-color);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.nav__link:hover {
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

.nav__icon {
    font-size: 1.2rem;
}

.nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

.nav__close:hover {
    color: hsl(250, 57%, 53%);
    color: var(--first-color-alt);
}

/* show menu */
.show-menu {
    bottom: 0;
}

/* Active link */
.active-link {
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

/* Change background header */
.scroll-header {
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
}

/*==================== HOME ====================*/
.home__container {
    grid-gap: 1rem;
    gap: 1rem;
}

.home__content {
    grid-template-columns: 0.5fr 3fr;
    padding-top: 3.5rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: -webkit-max-content;
    grid-template-columns: max-content;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: hsl(250, 69%, 61%);
    color: var(--first-color);
}

.home__social-icon:hover {
    color: hsl(250, 57%, 53%);
    color: var(--first-color-alt);
}

.home__blob {
    width: 200px;
    fill: hsl(250, 69%, 61%);
    fill: var(--first-color);
}

.home__blob-img {
    width: 170px;
}

.home__data {
    grid-column: 1/3;
}

.home__title {
    font-size: 2rem;
    font-size: var(--big-font-size);
    font-family: 'ShabnamBold', sans-serif;
    text-align: right;
}

.home__subtitle {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
    color: hsl(250, 8%, 45%);
    color: var(--text-color);
    font-weight: 500;
    font-weight: var(--font-medium);
    margin-bottom: 0.75rem;
    margin-bottom: var(--mb-0-75);
    font-family: 'ShabnamLight', sans-serif;
    text-align: right;
}

.home__description {
    margin-bottom: 2rem;
    margin-bottom: var(--mb-2);
