    font-weight: 500;
    font-weight: var(--font-medium);
    margin-right: 0.25rem;
    margin-right: var(--mb-0-25);
}

.home__scroll-arrow {
    font-size: 1.25rem;
}

/*==================== BUTTONS ====================*/
.button {
    display: inline-block;
    background-color: hsl(250, 69%, 61%);
