.blog-wrap {
  max-width: 700px;
  margin: 0 auto;
  
  padding: 1.5rem;
}

.main-blog-div {
  margin-top: 15vh;
}

.blog-wrap header {
  text-align: center;
}

.blog-date {
  font-size: 0.8rem;
  color: #FFFF;
  font-weight: 500;
}

.blog-wrap img {
  width: 100%;
  border-radius: 30px
}

.blog-subCategory {
  display: flex;
  justify-content: center;
}
.blog-subCategory > div {
  margin: 1rem;
}
.blog-desc {
  padding: 1rem;
  margin-top: 1.5rem;
  text-align: right;
  color: #FFFF
}

.blog-title {
  color : #FFF
}

.goBack {
  padding: 12px 50px 12px 50px;
  background-color: white;
  display: flex;
  width: fit-content;
  font-size: 1rem;
  color: #000000;
  justify-content: right;
  border-radius: 40px;
}
.blog-goBack {
  padding: 1rem;
    text-decoration: none;
   
    color: #FFF;
    font-weight: 500;
    display: flex;
    justify-content: center;
}


@media screen and (max-width: 768px) {
  .goBack {
    display: flex;
    justify-content:center;
    align-items: center;
  }
}