@import url('https://fonts.googleapis.com/css2?family=Francois+One&display=swap');


.navbar {
  
  /* height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
   */
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 18px 80px;
  z-index: 99;
}

.navbar-logo {
  /* color: #fff;
  justify-self: start;
  cursor: pointer;
  margin-left: 20px;
  font-size: 2rem;
  text-decoration: none;
  */
  width: 100%;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  font-size: 2rem;
  font-family: 'Francois One', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: center;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
  width: max-content;
}

.nav-links {
  text-decoration: none;
  padding: 8px 16px;
  color: #FFF;
  border-radius: 20px;
  transition: all 0.2s ease-out;
}

.nav-links:hover {
  background: #fff;
  color: #2b1055;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 880px) {
  .navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 18px 50px;
  }
  .navbar:focus {
  }
  .nav-item { 
   width: 100%;
  }
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #1888ff;
    border-radius: 0;
  }


  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #1888ff;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;

  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }

  button {
    display: none;
  }
}
